.breadcrum{
    display: flex;
    align-items: center;
    gap: 8px;
    color: rgb(27, 10, 0);
    font-size: 12px;
    margin: 110px 130px 0;
    font-weight: 500;
    text-transform: capitalize;
}

@media screen and (max-width: 600px){
    .breadcrum{
        margin: 110px 30px 0;
    }
}